.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}

.header__img {
  width: 100%;
  height: 100%;
}

.card-cont {
  box-sizing: border-box;
  color: #060b35;
  padding-inline: 16px;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  font-family: Roboto, sans-serif;
  transition-duration: .3s;
  display: flex;
}

.card-cont:hover {
  transform: scale(1.2);
}

.card-cont:hover .card-cont__title {
  color: #34568b;
}

.card-cont__img {
  width: 160px;
  min-height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card-cont__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card-cont__prod-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card-cont__review-cont {
  justify-content: space-between;
  width: 100%;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card-cont__review-count {
  text-align: right;
  font-size: 10px;
  line-height: 16px;
}

.card-cont__prod-price {
  justify-content: space-between;
  height: 18px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card-cont__price-title {
  color: #616070;
  font-size: 12px;
}

.card-cont__price-cur {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card-cont__button-buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card-cont__button-buy:hover {
  color: #00acdc;
  background: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.nav__list {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  justify-content: center;
  margin-left: 20px;
  display: flex;
  position: relative;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .5s;
}

.nav__link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  pointer-events: none;
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

* {
  margin: 0;
}

.cards-cont {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-cont {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .cards-cont {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .cards-cont {
    grid-template-columns: repeat(4, 200px);
  }
}

